  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
}

.movie-modal--close-btn {
  width: 35px;
  font-weight: bold;
  font-style: bolder;
  cursor: pointer;
  background-color: #fff;
  border: none;
  position: relative;
