  align-items: center;
  display: flex;
}

.add-to-watched--btn {
  box-sizing: border-box;
  width: 136px;
  height: 40px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #ff6b08;
  border: none;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

.add-to-watched--btn:hover {
  box-sizing: border-box;
  width: 136px;
  height: 40px;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  background-color: #fff;
